body {
  font: 14px 'Helvetica Neue', Helvetica, Arial, sans-serif;
  line-height: 1.4em;
  background: #f5f5f5;
  color: #4d4d4d;
  min-width: 230px;
  max-width: 550px;
  margin: 0 auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 300;
}

h1 {
  font-size: 100px;
  font-weight: 100;
  text-align: center;
  color: rgba(45, 20, 200, 0.25);
  text-rendering: optimizeLegibility;
}

:focus {
  outline: 0;
}

::-webkit-input-placeholder {
  font-style: italic;
  font-weight: 300;
  color: #a3a3a3;
}

::-moz-placeholder {
  font-style: italic;
  font-weight: 300;
  color: #a3a3a3;
}

:-ms-input-placeholder {
  font-style: italic;
  font-weight: 300;
  color: #a3a3a3;
}

::-ms-input-placeholder {
  font-style: italic;
  font-weight: 300;
  color: #a3a3a3;
}

::placeholder {
  font-style: italic;
  font-weight: 300;
  color: #a3a3a3;
}

.notepad {
  background: #fff;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2),
  0 25px 50px 0 rgba(0, 0, 0, 0.1);
}

.new-todo {
  width: 100%;
  font-size: 24px;
  font-family: inherit;
  font-weight: inherit;
  line-height: 1.4em;
  border: 0;
  color: inherit;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 16px 16px 16px 60px;
  background: rgba(0, 0, 0, 0.003);
  box-shadow: inset 0 -2px 1px rgba(0,0,0,0.03);
}

.list {
  position: relative;
  border-top: 1px solid #e6e6e6;
}

.list:before {
  content: '';
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  height: 50px;
  overflow: hidden;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2),
  0 8px 0 -3px #f6f6f6,
  0 9px 1px -3px rgba(0, 0, 0, 0.2),
  0 16px 0 -6px #f6f6f6,
  0 17px 2px -6px rgba(0, 0, 0, 0.2);
}


.todo-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.todo-list li {
  position: relative;
  font-size: 24px;
  border-bottom: 1px solid #ededed;
}

.todo-list li:last-child {
  border-bottom: none;
}

.todo-list li .inner {
  word-break: break-all;
  padding: 15px 15px 15px 60px;
  line-height: 1.2;
  -webkit-transition: color 0.4s;
  transition: color 0.4s;
}
.todo-list li .remove {
  display: none;
  position: absolute;
  top: 0;
  right: 10px;
  bottom: 0;
  width: 40px;
  height: 40px;
  margin: auto 0 auto 11px;
  font-size: 30px;
  color: #cc9a9a;
  -webkit-transition: color 0.2s ease-out;
  transition: color 0.2s ease-out;
  cursor: pointer;
  padding: 0;
  border: 0;
  background: none;
  vertical-align: baseline;
  font-family: inherit;
  font-weight: inherit;
  -webkit-appearance: none;
  -moz-appearance: none;
       appearance: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.todo-list li .remove:hover {
  color: #af5b5e;
}

.todo-list li .remove:after {
  content: '×';
}

.todo-list li:hover .remove {
  display: block;
}
